<template>
    <el-container>
        <!--顶部-->
        <el-header style="border-bottom: 1px solid gray;">
            <el-row style="margin: 10px 15px">
                <el-col :span="1">
                    <!--收缩条-->
                    <a href="#" @click="changeCollapse" style="font-size: 35px;color:#909399;"><i
                            :class="collpaseIcon"></i></a>
                </el-col>
            </el-row>
        </el-header>
        <!--中央区域-->
        <el-main>
            <el-container>
                <!--左侧导航栏-->
                <el-aside :style="{width:collpaseWidth}">
                    <!--default-active代表导航栏默认选中哪个index, :collapse决定导航栏是否展开，为boolean类型
                    :router决定导航栏是否开启路由模式，即在菜单item上设置路由是否生效，值为boolean类型-->
                    
                    <el-menu
                            default-active="0"
                            class="el-menu-vertical-demo"
                            :collapse="isCollapse"
                            :router="true"
                            text-color="#fff"
                            active-text-color="#ffd04b"
                            
                    >
                        <!--index设置当前item的下标，:route则是传一个对象进行，指定路由-->
                        <el-menu-item index="0" :route="{name:'Index'}">
                            <i class="fa fa-magic"></i>
                            <span slot="title"> 首页</span>
                        </el-menu-item>

                        <el-submenu index="1">
                            <template slot="title">
                                <i class="fa fa-users"></i>
                                <span> 合同管理</span>
                            </template>

                            <el-menu-item index="1-1" :route="{name:'draft'}"><i class="fa fa-cog"></i> 
                            合同起草 </el-menu-item>
                            <el-menu-item index="1-2" :route="{name:'countersign'}"><i class="fa fa-cog"></i> 
                            合同会签 </el-menu-item>
                            <el-menu-item index="1-3" :route="{name:'finalize'}"><i class="fa fa-cog"></i> 
                            合同定稿 </el-menu-item>
                            <el-menu-item index="1-4" :route="{name:'audit'}"><i class="fa fa-cog"></i> 
                            合同审批 </el-menu-item>
                            <el-menu-item index="1-5" :route="{name:'sign'}"><i class="fa fa-cog"></i> 
                            合同签订 </el-menu-item>
                            <el-menu-item index="1-6" :route="{name:'flow_path'}"><i class="fa fa-cog"></i> 
                            合同查询 </el-menu-item>
                            <el-menu-item index="1-7" :route="{name:'allocate_contract'}"><i class="fa fa-cog"></i> 
                            合同分配 </el-menu-item>
                        </el-submenu>


                        
                    <el-submenu index="2">
                            <template slot="title">
                                <i class="fa fa-users"></i>
                                <span> 客户管理</span>
                            </template>

                            <el-menu-item index="2-1" :route="{name:'addcustomer'}"><i class="fa fa-cog"></i> 
                            添加客户 </el-menu-item>
                            <el-menu-item index="2-2" :route="{name:'managecustomer'}"><i class="fa fa-cog"></i> 
                            管理客户 </el-menu-item>
                        </el-submenu>

                    <el-submenu index="3">
                            <template slot="title">
                                <i class="fa fa-users"></i>
                                <span> 权限管理</span>
                            </template>

                            <el-menu-item index="3-1" :route="{name:'draft'}"><i class="fa fa-cog"></i> 
                            添加用户 </el-menu-item>
                            <el-menu-item index="3-2" :route="{name:'countersign'}"><i class="fa fa-cog"></i> 
                            修改用户权限 </el-menu-item>
                            <el-menu-item index="3-3" :route="{name:'countersign'}"><i class="fa fa-cog"></i> 
                            删除用户 </el-menu-item>
                            <el-menu-item index="3-4" :route="{name:'countersign'}"><i class="fa fa-cog"></i> 
                            查询用户 </el-menu-item>
                        </el-submenu>
                    </el-menu>



                </el-aside>
                <!--主内容显示区域-->
                <el-main>
                    <!--路由渲染-->
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-main>
        <!--底部-->
        
    </el-container>
</template>

<script>
    // 这一大段JS就是为了做收缩/展开导航栏而用的！
    export default {
        name: "Main",
        data: function () {
            return {
                isCollapse: false, // 决定左侧导航栏是否展开
            }
        },
        computed: {
            collpaseIcon: function () { // 左侧导航栏是否展开状态的图标
                // 如果是展开状态就图标向右，否则图标向左
                return this.isCollapse ? 'el-icon-s-fold' : 'el-icon-s-unfold';
            },
            collpaseWidth: function () { // 左侧导航栏是否展开状态的宽度
                // 如果是展开状态就导航栏宽度为65px，否则200px
                return this.isCollapse ? '65px' : '200px';
            }
        },
        methods: {
            changeCollapse: function () { // 更改左侧导航栏展示状态
                this.isCollapse = !this.isCollapse;
            }
        }
    }
</script>

<style scoped>
    /*整体显示区域布局样式*/
    .el-container {
        height: 100%;
    }
    .el-header {
        padding: 0;
        background-color:rgb(46, 45, 44);
        
    }
    .el-main {
        padding: 0;
    }
    /*左边导航栏具体样式*/
    .el-menu-vertical-demo.el-menu {
        padding-left: 20px;
        text-align: left;
        height: 100%;
        padding: 0;
        background-color:rgb(47, 53, 63);
    }
    el-container > .el-menu-vertical-demo.el-menu {
        padding: 0;
    }
    .el-submenu .el-menu-item, .el-menu-item {
        background-color:rgb(47, 53, 63);
        min-width: 50px;
    }
    .el-menu-item {
        padding: 0;
    }
    .el-submenu-item {
        padding: 0;
        background-color:rgb(47, 53, 63);
    }

    
</style>